/* API Key Pool Repeater Styles */
.humata-api-key-pool {
    max-width: 500px;
}

.humata-api-key-pool__keys {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 10px;
}

.humata-api-key-pool__row {
    display: flex;
    align-items: center;
    gap: 8px;
}

.humata-api-key-pool__input {
    flex: 1;
}

.humata-api-key-pool__remove {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 36px;
    padding: 0 8px;
    color: #646970;
}

.humata-api-key-pool__remove:hover {
    color: #d63638;
    border-color: #d63638;
}

.humata-api-key-pool__remove .dashicons {
    font-size: 16px;
    width: 16px;
    height: 16px;
    line-height: 16px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
}

.humata-api-key-pool__add {
    display: inline-flex;
    align-items: center;
    gap: 4px;
}

.humata-api-key-pool__add .dashicons {
    font-size: 16px;
    width: 16px;
    height: 16px;
    line-height: 16px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
}

.humata-api-key-pool__count {
    display: inline-block;
    margin-left: 12px;
    padding: 4px 10px;
    background: #e7f3e8;
    border-radius: 12px;
    font-size: 12px;
    color: #1d6f42;
    font-weight: 500;
}

/* Documents Tab - Wider containers */
.humata-documents-tab .card {
    max-width: 1200px;
}

.humata-documents-tab .wp-list-table {
    table-layout: auto;
}

.humata-documents-tab .wp-list-table th,
.humata-documents-tab .wp-list-table td {
    white-space: nowrap;
}

.humata-documents-tab .wp-list-table td:first-child,
.humata-documents-tab .wp-list-table th:first-child {
    white-space: normal;
    word-break: break-word;
}

/* Bulk actions bar */
.humata-bulk-actions {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 8px 0;
    flex-wrap: wrap;
}

.humata-bulk-actions .button {
    margin: 0;
}

.humata-bulk-select-info {
    color: #646970;
    font-style: italic;
}

/* Checkbox column */
.humata-documents-tab .check-column {
    width: 2.2em;
    padding: 10px 0 10px 3px;
    vertical-align: middle;
}

.humata-documents-tab .check-column input[type="checkbox"] {
    margin: 0;
    vertical-align: middle;
}

/* Category dropdown in table */
.humata-documents-tab .humata-cat-select {
    width: auto;
    min-width: 120px;
    max-width: 180px;
}

/* Test search results table */
#humata-test-search-results {
    table-layout: fixed;
    width: 100%;
}

#humata-test-search-results th:nth-child(1),
#humata-test-search-results td:nth-child(1) {
    width: 20%;
}

#humata-test-search-results th:nth-child(2),
#humata-test-search-results td:nth-child(2) {
    width: 20%;
}

#humata-test-search-results th:nth-child(3),
#humata-test-search-results td:nth-child(3) {
    width: 50%;
}

#humata-test-search-results th:nth-child(4),
#humata-test-search-results td:nth-child(4) {
    width: 10%;
}

#humata-test-search-results th,
#humata-test-search-results td {
    white-space: normal;
    word-wrap: break-word;
    overflow-wrap: break-word;
}

